<!-- 简历列表 -->
<template>
    <div class="box">
        <div class="funcBox">
            <div class="choiceBox">
                <el-select v-model="workingYears" placeholder="工作年数" class="choice">
                    <el-option v-for="item in workYearsC" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
                <el-select v-model="education" placeholder="教育背景" class="choice">
                    <el-option v-for="item in eduBackgroundC" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
                <el-select v-model="age" placeholder="年龄" class="choice">
                    <el-option v-for="item in ageC" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
                <el-button type="primary" @click="search">查询</el-button>
                <el-button @click="resetForm">重置</el-button>
            </div>
            <div class="searchBox">
                <el-form ref="form" size="medium" class="searchForm">
                    <el-form-item>
                        <el-input v-model="inputValue" placeholder="可输入简历关键词搜索" @input="valueChange"></el-input>
                    </el-form-item>
                </el-form>
                <div class="result">
                    <ul v-if="searchList.length">
                        <li v-for="(i, index) in searchList" :key="index" @click="goDetail(i.id)">{{ i.name }} {{ i.age }} {{ i.education }} 
                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="resumeBox">

            <div class="resume" :style="{ display: hidden }" v-for="i in upLoadList" :key="i.id">
                <div class="name">{{ i.name }}</div>
                <div class="age"><span>年龄：{{ i.age }}</span> <span>工作年限：{{ i.workingYears }}</span> <span>学历：{{
                    i.education
                }}</span></div>
                <div class="want"><span>求职期望：{{ i.career }} </span></div>
                <div class="honor"><span>荣誉证书：{{ i.honor }}</span></div>
                <div class="ex">主要经历：{{ i.workExperience }}</div>
                <el-rate :value="score(i.total)" disabled text-color="#ff9900" class="score">
                </el-rate>
                <el-button type="primary" class="details" size="mini" @click="goDetail(i.id)">查看详情</el-button>
                <el-button type="info" class="cancel" size="mini" @click="hide(i.id)">删除此项</el-button>
            </div>
        </div>
        <div class="pages" v-if="this.total">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize"
                class="pagesContainer" @current-change="pageChange">
            </el-pagination>
        </div>
    </div>
</template>

<script>
// import { reqUserResume } from '@/api';
import { reqUserUpload, reqDelResume, reqSearch, reqSearchCv } from '@/api'
export default {
    name: 'upload',
    data() {
        return {
            searchList: [],
            inputValue: '',
            pageType: 0,
            pageSize: 4,
            total: 0,
            hidden: 'block'
            ,
            formInline: {
                user: "",
                region: ""
            },
            workYearsC: [{
                value: '1',
                label: '0'
            }, {
                value: '2',
                label: '1-2年'
            }, {
                value: '3',
                label: '3-5年'
            }, {
                value: '4',
                label: '6-8年'
            }, {
                value: '5',
                label: '8-16年'
            }, {
                value: '6',
                label: '16及以上年'
            }],
            workingYears: '',
            eduBackgroundC: [{
                value: '1',
                label: '大专以下'
            }, {
                value: '2',
                label: '大专'
            }, {
                value: '3',
                label: '本科'
            }, {
                value: '4',
                label: '硕士'
            }, {
                value: '5',
                label: '博士'
            }, {
                value: '6',
                label: '博士后'
            }],
            education: '',
            ageC: [{
                value: '1 ',
                label: '18-22 '
            }, {
                value: '2 ',
                label: '23-27 ',
            }, {
                value: '  3 ',
                label: '28-32  ',
            },
            {
                value: '4',
                label: '33-37   ',
            },
            {
                value: ' 5 ',
                label: '38-42',
            }, {
                value: '6',
                label: '43-47'
            }, {
                value: '7',
                label: '48+'
            }],
            age: '',
            area: '',
            id: '',
            upLoadList: [],
            timer:null
        };
    },
    computed: {
        score() {
            return function (e) {
                return Math.floor(e / 5)
            }
        }
    },
    methods: {
        async valueChange(e) {
            
            if(this.timer) clearTimeout(this.timer)
            this.timer = setTimeout(async () => {
                this.inputValue = e
                const res = await reqSearchCv(localStorage.getItem('id'), e)
                if (res.code == 1) {
                    this.searchList = res.data
                }
            }, 1000);

        },
        async pageChange(e) {

            if (this.pageType == 0) {
                const res = await reqUserUpload(localStorage.getItem('id'), e)
                if (res.code == 1) {
                    this.upLoadList = res.data
                }
            } else {
                const { workingYears, education, age } = this
                const data = { workingYears, education, age }
                const res = await reqSearch(localStorage.getItem('id'), data, e)
                if (res.code == 1) {
                    this.upLoadList = res.data
                }
            }
        },
        onSubmit() {
            console.log("submit!");
        },
        goDetail(Id) {
            this.$router.push({ name: 'Analyze', params: { id: Id } })
        },
        async hide(id) {
            const result = confirm('确定删除吗');
            if (result) {
                const res = await reqDelResume(id)
                if (res.code == 1) {
                    this.$message({
                        message: '删除成功',
                        type: 'success'
                    });
                    this.getUserUpload()
                }
            } else {
                // 用户点击了取消按钮
                // 执行其他操作或不执行任何操作
            }

        },
        async getUserUpload() {
            const res = await reqUserUpload(localStorage.getItem('id'), 0)
            if (res.code == 1) {
                this.total = res.data.length
                if (this.total > 4) {
                    this.upLoadList = res.data.slice(0, 4)
                } else {
                    this.upLoadList = res.data.slice(0, this.total)
                }

            }
        },
        resetForm() {
            this.pageType = 0
            this.workingYears = '',
                this.education = '',
                this.age = '',
                this.getUserUpload()
        },
        async search() {
            this.pageType = 1
            const id = localStorage.getItem('id')
            const { workingYears, education, age } = this
            const data = { workingYears, education, age }
            const res = await reqSearch(id, data, 0)

            if (res.code == 1) {
                this.total = res.data.length
                if (this.total > 4) {
                    this.upLoadList = res.data.slice(0, 4)
                } else {
                    this.upLoadList = res.data.slice(0, this.total)
                }
            }
        }
    },
    mounted() {
        this.getUserUpload()
    }
}
</script>
<style lang="scss" scoped>
::v-deep .el-input__inner {
    border-color: black;
    color: black;

    &::placeholder {
        color: grey;
    }
}

.box {
    width: 80%;
    float: left;
    margin-left: 20px;
    margin-top: 50px;
    margin-bottom: 100px;

    .pages {
        width: 100%;
        height: 100px;
        position: relative;

        .pagesContainer {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .funcBox {
        width: 100%;
        height: 100px;
        display: flex;
        justify-content: space-around;
    }

    .choiceBox {
        width: 60%;
        height: 50px;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .choice {
            scale: 0.9;
        }

        // .choice {
        //     border: 1px black solid;
        //     border-radius: 5%;
        // }
    }

    .searchBox {
        width: 350px;
        margin-top: 9px;
        position: relative;

        .result {
            position: absolute;
            top: 40px;
            z-index: 1;
            width: 100%;
            border: 1px solid rgb(0, 0, 0, 0.2);

            ul {
                li {
                    line-height: 40px;
                    border-bottom: 1px solid rgb(0, 0, 0, 0.2);
                    text-indent: 10px;
                    font-size: 15px;
                    height: 40px;
                    background-color: #fff;
                    cursor: pointer;
                }
            }
        }
    }

    .resumeBox {
        width: 100%;
        height: 700px;
    }

    .resume {
        width: 100%;
        border: 1px solid black;
        height: 150px;
        margin-top: -20px;
        margin-bottom: 30px;
        border-radius: 10px;
        position: relative;

        .name {
            position: absolute;
            top: 15%;
            left: 8%;
            font-weight: 700;
            font-size: 15px;
        }

        .age {
            position: absolute;
            top: 35%;
            left: 90px;
            width: 250px;

            span {
                margin-left: 10px;
            }
        }

        .want {
            position: absolute;
            top: 55%;
            width: 250px;
            left: 90px;

            span {
                margin-left: 10px;
            }
        }

        .honor {
            position: absolute;
            top: 75%;
            width: 250px;
            left: 90px;


            span {
                margin-left: 10px;
                height: 30px;
                display: block;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .ex {
            width: 40%;
            height: 55%;
            top: 30%;
            left: 40%;
            position: absolute;
            background-color: rgb(246, 249, 252);
            overflow-y: scroll;
        }

        .score {
            position: absolute;
            left: 15%;
            top: 15%;
        }

        .details {
            position: absolute;
            right: 5%;
            top: 30%;
        }

        .cancel {
            position: absolute;
            right: 5%;
            top: 65%;
        }
    }

}
</style>